





















// 医生 - 获取所有医生信息
export const getDoctorListAPI = () => {
	return uni.request({
	  url: 'http://127.0.0.1:3007/api/getAllDoctor',
	  method: 'GET',
	  // header: { 'content-type': 'multipart/form-data' }
	})
}

// 诊疗 - 智能诊疗
export const zhenliaoAPI = (symptoms) => {
	return uni.request({
		url: `/api/treat?symptoms=${symptoms}`,
		method: 'GET'
	})
}
